import React from 'react';
import { connect } from 'react-redux';
import { Switch, Slider } from 'antd';
import { 
  toggleBoxedLayout,
  toggleCollapsedNav,
  toggleOffCanvasNav,
  toggleFixedHeader,
  changeSidebarWidth
} from '../../../actions';


class LayoutOptions extends React.Component {
  constructor(props) {
    super(props);
  }

  onToggleFixedHeader = (isChecked) => {
    const { handleToggleFixedHeader } = this.props;
    handleToggleFixedHeader(isChecked);
  }

  onToggleCollapsedNav = (isChecked) => {
    const { handleToggleCollapsedNav } = this.props;
    handleToggleCollapsedNav(isChecked);
  }

  onToggleOffCanvasNav = (isChecked) => {
    const { handleToggleOffCanvasNav } = this.props;
    handleToggleOffCanvasNav(isChecked);
  }

  onToggleBoxedLayout = (isChecked) => {
    const { handleToggleBoxedLayout } = this.props;
    handleToggleBoxedLayout(isChecked);
  }

  onSidebarWidthChange = (val) => {
    const { handleSidebarWidthChange } = this.props;
    handleSidebarWidthChange(val);
  }

  render() {
    const { boxedLayout, fixedHeader, collapsedNav, offCanvasNav, sidebarWidth } = this.props;

    return (
      <section className="customizer-layout-options">
        <h4 className="section-header">Layout Options</h4>
        <div className="divider divider-md"></div>

        <div className="layout-option-item">
          Fixed Header
          <Switch checked={fixedHeader} onChange={this.onToggleFixedHeader} />
        </div>
        <div className="layout-option-item">
          Collapsed Sidebar
          <Switch checked={collapsedNav} onChange={this.onToggleCollapsedNav} />
        </div>
        <div className="layout-option-item">
          Off-Canvas Sidebar
          <Switch checked={offCanvasNav} onChange={this.onToggleOffCanvasNav} />
        </div>
        <div className="layout-option-item">
          Boxed Layout
          <Switch checked={boxedLayout} onChange={this.onToggleBoxedLayout} />
        </div>
        <div className="row layout-option-item-slider">
          <div className="col-4">Sidebar Width</div>
          <div className="col-8">
            <Slider
              min={180}
              max={280}
              onChange={this.onSidebarWidthChange}
              value={sidebarWidth}
              tipFormatter={ (value) => {return `${value}px`;} }
            />
          </div>
        </div>

      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    boxedLayout: state.settings.boxedLayout,
    fixedHeader: state.settings.fixedHeader,
    collapsedNav: state.settings.collapsedNav,
    offCanvasNav: state.settings.offCanvasNav,
    sidebarWidth: state.settings.sidebarWidth
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    handleToggleFixedHeader: (isFixedHeader) => {
      dispatch( toggleFixedHeader(isFixedHeader) );
    },
    handleToggleCollapsedNav: (isCollapsedNav) => {
      dispatch( toggleCollapsedNav(isCollapsedNav) );
    },
    handleToggleOffCanvasNav: (isOffCanvasNav) => {
      dispatch( toggleOffCanvasNav(isOffCanvasNav) );
    },
    handleToggleBoxedLayout: (isBoxedLayout) => {
      dispatch( toggleBoxedLayout(isBoxedLayout) );
    },
    handleSidebarWidthChange: (sidebarWidth) => {
      dispatch( changeSidebarWidth(sidebarWidth) );
    }
  }
}

module.exports = connect(
  mapStateToProps,
  mapDispatchToProps
)(LayoutOptions);



